import debounce from "lodash.debounce";
import { updateRecipes } from "./searchRecipes";
import Choices from "choices.js";
import "choices.js/public/assets/styles/choices.min.css";

let choicesInstance;

const searchInput = document.getElementById("search-recipes");
const searchByTime = document.getElementById("search-by-time");
const searchByArea = document.getElementById("search-by-area");
const searchByIngredient = document.getElementById("search-by-ingredients");

export function initFilters() {
  if (searchInput) {
    searchInput.addEventListener("input", debounce(updateRecipes, 300));
  }

  if (searchByTime) {
    searchByTime.addEventListener("change", updateRecipes);
  }

  if (searchByArea) {
    searchByArea.addEventListener("change", updateRecipes);
  }

  if (searchByIngredient) {
    searchByIngredient.addEventListener("change", updateRecipes);
  }
  if (choicesInstance) {
    choicesInstance.destroy();
  }

  // Initialize Choices
  choicesInstance = new Choices(searchByTime, {
    removeItemButton: false,
    placeholderValue: "Select time",
    searchPlaceholderValue: "Type to search",
    searchEnabled: true,
    shouldSort: false,
    duplicateItemsAllowed: false,
  });
}
