import Choices from "choices.js";
import "choices.js/public/assets/styles/choices.min.css";
import { fetchIngredientsList } from "./fetchData";

let choicesInstance;

function ingredientList(ingredientList) {
  const ingredientSelect = document.getElementById("search-by-ingredients");

  // Clear previous options
  ingredientSelect.innerHTML = "";

  // Add options from the fetched list
  ingredientList.forEach((element) => {
    const option = document.createElement("option");
    option.textContent = element.name;
    option.value = element.name;
    ingredientSelect.appendChild(option);
  });

  // Destroy old instance if needed
  if (choicesInstance) {
    choicesInstance.destroy();
  }

  // Initialize Choices
  choicesInstance = new Choices(ingredientSelect, {
    removeItemButton: false,
    placeholderValue: "Select ingredients",
    searchPlaceholderValue: "Type to search",
    searchEnabled: true,
    shouldSort: false,
    duplicateItemsAllowed: false,
  });
}

export function ingredientListInit() {
  fetchIngredientsList()
    .then((data) => {
      console.log("Parsed JSON data:", data);
      ingredientList(data);
    })
    .catch((error) => {
      console.error("Error fetching ingredients:", error);
    });
}
