import { categoriesListDisplay } from "./js/categoriesDisplay";
import { ingredientListInit } from "./js/ingredientsDispaly";
import { initFilters } from "./js/initFilters";
import { masterClassDisplayInit } from "./js/masterclassDisplay";
import { orderNowModal } from "./js/orderNowModal";
import { displayPopularRecipes } from "./js/popularRecipesDisplay";
import { regionListInit } from "./js/regionsDisplay";
import { updateRecipes } from "./js/searchRecipes";
import { sidebar } from "./js/sidebar";
import { theme } from "./js/theme";

document.addEventListener("DOMContentLoaded", function () {
  try {
    masterClassDisplayInit();
    categoriesListDisplay();
    displayPopularRecipes();
    ingredientListInit();
    regionListInit();
    theme();
    sidebar();
    orderNowModal();
    initFilters()
    updateRecipes()
  } catch (error) {
    console.error("Error in script:", error);
  }
});
